.header {
    height: 110px;
    background-color: rgb(40, 161, 161);
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    width: 100%;

    &__logo {
        width: 80px;
        margin-left: 40px;
    }

    &__inner {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__login {
        margin-right: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 170px;
        a {
            color: #fff;
        }
        img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
        }
    }
}

